@mixin font($font-size, $font-weight) {
  font-weight: $font-weight;
  font-size: $font-size;
}
/* stylelint-disable */
@mixin number-styles($color, $font-size, $font-weight, $letter-spacing, $ttu, $transform) {
  /* stylelint-enable */
  color: $color;
  font-size: $font-size;
  font-weight: $font-weight;
  letter-spacing: $letter-spacing;
  text-transform: $ttu;
  transform: $transform;
}
/* stylelint-disable */
@mixin contact-us-styles($color, $font-size, $font-weight, $letter-spacing, $ttu,)
/* stylelint-disable */ {
  color: $color;
  font-size: $font-size;
  font-weight: $font-weight;
  letter-spacing: $letter-spacing;
  text-transform: $ttu;
}
